import React from 'react'
import profile from "../components/images/profile.jpg";

const Home = () => {
  return (
    <>

    <div className=" h-full min-h-screen    flex grow bg-gradient-to-t flex-col justify-center items-center   text-left ">
    <header className='text-xl xl:text-5xl tracking-widest  transition  motion-safe:animate-fadeIn flex-shrink  '>Hello!<br></br> Welcome to my website!</header>

    <img
      class="w-64 h-64 drop-shadow-3xl m-8 rounded-full transition ease-in-out delay-700  hover:-translate-y-1 hover:scale-100   motion-safe:animate-fadeIn "
      src={profile}
      alt="Rounded avatar"
    /><div className=''>
    <p class="text-xl  p-2 hover:drop-shadow-4xl  flex-auto max-w-3xl ">
      I'm Leijan Legaspi and I like
      being creative. I have a degree in computer science and taken
      multiple graphic design courses.<br></br> Here are some technologies I have worked with.
      <ul class="list-disc columns-3 text-sm marker:text-green-800 list-inside">
      <li >React</li> 
      <li >Node.js</li> 
      <li >Javascript</li> 
      <li >React Tables</li> 
      <li >R</li> 
      <li >Android Studio</li> 
      <li >Adobe xd</li> 
      <li >Photoshop</li> 
      <li >Jquery</li>
      </ul>
    </p>
    
    <p className='p-2'>please
      hire me.<br></br> Please check out my current and future projects on the dropdown menu above.</p>
    </div>
    
  </div>
  </>
    )
}

export default Home